@use "../../z-index/index.scss" as *;

@mixin kendo-virtual-scroller--layout-base() {

    // Virtual scroller
    .k-virtual-scroller {
        width: 100%;
        height: 100%;
        flex: 1 1 auto;
        overflow: auto;
        position: relative;
    }


    // Virtual scroller wrap
    .k-virtual-scroller-wrap {
        position: relative;
        z-index: k-z-index("base", 1);
    }


    // Virtual scroller content
    .k-virtual-scroller-content {
        position: absolute;
        width: 100%;
        inset-block-start: 0;
        inset-inline-start: 0;
    }


    // Virtual scroller size
    .k-virtual-scroller-size {
        position: relative;
        z-index: k-z-index("base");
    }

}
