@use "@progress/kendo-theme-core/scss/components/editor/_layout.scss" as *;


@mixin kendo-editor--layout() {

    @include kendo-editor--layout-base();

    // Insert table
    .k-ct-popup {
        line-height: var( --kendo-line-height, normal);

        .k-status {
            line-height: var( --kendo-line-height, normal);
        }
    }
}
