@use "../../mixins/index.scss" as *;
@use "./variables.scss" as *;

@mixin kendo-pager--theme-base() {

    // Pager
    .k-pager {
        @include fill(
            $kendo-pager-text,
            $kendo-pager-bg,
            $kendo-pager-border
        );

        &:focus,
        &.k-focus {
            @include fill( $bg: $kendo-pager-focus-bg );
            @include focus-indicator( $kendo-pager-focus-shadow, true );
        }
    }

}
