@use "../core/_index.scss" as *;
@use "../icons/_variables.scss" as *;
@use "../input/_variables.scss" as *;


// Spreadsheet

/// The width of the border around the Spreadsheet.
/// @group spreadsheet
$kendo-spreadsheet-border-width: 1px !default;
/// The font family of the Spreadsheet.
/// @group spreadsheet
$kendo-spreadsheet-font-family: var( --kendo-font-family, inherit ) !default;
/// The font size of the Spreadsheet.
/// @group spreadsheet
$kendo-spreadsheet-font-size: var( --kendo-font-size, inherit ) !default;
/// The line height of the Spreadsheet.
/// @group spreadsheet
$kendo-spreadsheet-line-height: var( --kendo-line-height, normal ) !default;

/// The background color of the Spreadsheet.
/// @group spreadsheet
$kendo-spreadsheet-bg: k-color(surface) !default;
/// The text color of the Spreadsheet.
/// @group spreadsheet
$kendo-spreadsheet-text: k-color(on-app-surface) !default;
/// The border color of the Spreadsheet.
/// @group spreadsheet
$kendo-spreadsheet-border: k-color(border) !default;

/// The background color of the Spreadsheet header.
/// @group spreadsheet
$kendo-spreadsheet-header-bg: k-color(surface) !default;
/// The text color of the Spreadsheet header.
/// @group spreadsheet
$kendo-spreadsheet-header-text: k-color(on-app-surface) !default;
/// The border color of the Spreadsheet header.
/// @group spreadsheet
$kendo-spreadsheet-header-border: k-color(border) !default;
/// The gradient of the Spreadsheet header.
/// @group spreadsheet
$kendo-spreadsheet-header-gradient: none !default;

/// The background color of the Spreadsheet table headers.
/// @group spreadsheet
$kendo-spreadsheet-table-header-bg: k-color(surface) !default;
/// The text color of the Spreadsheet table headers.
/// @group spreadsheet
$kendo-spreadsheet-table-header-text: k-color(on-app-surface) !default;
/// The border color of the Spreadsheet table headers.
/// @group spreadsheet
$kendo-spreadsheet-table-header-border: k-color(border) !default;
/// The gradient of the Spreadsheet table headers.
/// @group spreadsheet
$kendo-spreadsheet-table-header-gradient: none !default;

/// The border width of the Spreadsheet action bar.
/// @group spreadsheet
$kendo-spreadsheet-action-bar-border-width: 1px !default;
/// The horizontal padding of the Spreadsheet action bar.
/// @group spreadsheet
$kendo-spreadsheet-action-bar-padding-y: k-spacing(1) !default;
/// The vertical padding of the Spreadsheet action bar.
/// @group spreadsheet
$kendo-spreadsheet-action-bar-padding-x: k-spacing(2) !default;
/// The font size of the Spreadsheet action bar.
/// @group spreadsheet
$kendo-spreadsheet-action-bar-font-size: $kendo-spreadsheet-font-size !default;
/// The font family of the Spreadsheet action bar.
/// @group spreadsheet
$kendo-spreadsheet-action-bar-font-family: Arial, Verdana, sans-serif !default;
/// The spacings of the Spreadsheet action bar.
/// @group spreadsheet
$kendo-spreadsheet-action-bar-spacing: k-spacing(2) !default;

/// The gap of the Spreadsheet formula bar.
/// @group spreadsheet
$kendo-spreadsheet-formula-bar-gap: k-spacing(2) !default;

/// The horizontal padding of the Spreadsheet formula input.
/// @group spreadsheet
$kendo-spreadsheet-formula-input-padding-x: $kendo-input-md-padding-x !default;
/// The vertical padding of the Spreadsheet formula input.
/// @group spreadsheet
$kendo-spreadsheet-formula-input-padding-y: $kendo-input-md-padding-y !default;
/// The line height of the Spreadsheet formula input.
/// @group spreadsheet
$kendo-spreadsheet-formula-input-line-height: $kendo-input-line-height !default;

/// The padding of the Spreadsheet formula bar icon.
/// @group spreadsheet
$kendo-spreadsheet-formula-bar-icon-padding: k-spacing(1) !default;

/// The font family of the Spreadsheet view.
/// @group spreadsheet
$kendo-spreadsheet-view-font-family: Arial, Verdana, sans-serif !default;
/// The font size of the Spreadsheet view.
/// @group spreadsheet
$kendo-spreadsheet-view-font-size: $kendo-spreadsheet-font-size !default;

/// The background color of the Spreadsheet selection.
/// @group spreadsheet
$kendo-spreadsheet-selection-bg: color-mix(in srgb, k-color(secondary-emphasis) 50%, transparent) !default;
/// The text color of the Spreadsheet selection.
/// @group spreadsheet
$kendo-spreadsheet-selection-text: null !default;
/// The border color of the Spreadsheet selection.
/// @group spreadsheet
$kendo-spreadsheet-selection-border: k-color(primary) !default;
/// The shadow of the Spreadsheet selection.
/// @group spreadsheet
$kendo-spreadsheet-selection-shadow: inset 0 0 0 1px k-color(primary) !default;

/// The background color of the Spreadsheet single selection.
/// @group spreadsheet
$kendo-spreadsheet-single-selection-bg: k-color(primary) !default;
/// The text color of the Spreadsheet single selection.
/// @group spreadsheet
$kendo-spreadsheet-single-selection-text: null !default;
/// The border color of the Spreadsheet single selection.
/// @group spreadsheet
$kendo-spreadsheet-single-selection-border: $kendo-spreadsheet-bg !default;

/// The background color of the Spreadsheet partial selection.
/// @group spreadsheet
$kendo-spreadsheet-partial-selection-bg: k-color(secondary) !default;

/// The background color of the Spreadsheet active cell.
/// @group spreadsheet
$kendo-spreadsheet-active-cell-bg: k-color(surface) !default;
/// The shadow of the Spreadsheet active cell.
/// @group spreadsheet
$kendo-spreadsheet-active-cell-shadow: inset 0 0 0 1px k-color(primary) !default;

/// The background color of the Spreadsheet auto fill.
/// @group spreadsheet
$kendo-spreadsheet-auto-fill-bg: color-mix(in srgb, k-color(primary) 25%, transparent) !default;
/// The text color of the Spreadsheet auto fill.
/// @group spreadsheet
$kendo-spreadsheet-auto-fill-text: null !default;
/// The border color of the Spreadsheet auto fill.
/// @group spreadsheet
$kendo-spreadsheet-auto-fill-border: k-color(primary) !default;
/// The shadow of the Spreadsheet auto fill.
/// @group spreadsheet
$kendo-spreadsheet-auto-fill-shadow: inset 0 0 0 1px k-color(primary) !default;
/// The background color of the Spreadsheet auto fill punch.
/// @group spreadsheet
$kendo-spreadsheet-auto-fill-punch-bg: color-mix(in srgb, k-color(app-surface) 50%, transparent) !default;

/// The vertical padding of the Spreadsheet cell.
/// @group spreadsheet
$kendo-spreadsheet-cell-padding-y: k-spacing(1) !default;
/// The horizontal padding of the Spreadsheet cell.
/// @group spreadsheet
$kendo-spreadsheet-cell-padding-x: $kendo-spreadsheet-cell-padding-y !default;

/// The line height of the Spreadsheet cell editor.
/// @group spreadsheet
$kendo-spreadsheet-cell-editor-line-height: 20px !default;
/// The horizontal padding of the Spreadsheet cell editor.
/// @group spreadsheet
$kendo-spreadsheet-cell-editor-padding-x: k-spacing(1) !default;
/// The vertical padding of the Spreadsheet cell editor.
/// @group spreadsheet
$kendo-spreadsheet-cell-editor-padding-y: k-spacing(0) !default;
/// The background color of the Spreadsheet cell editor.
/// @group spreadsheet
$kendo-spreadsheet-cell-editor-bg: $kendo-spreadsheet-bg !default;
/// The text color of the Spreadsheet cell editor.
/// @group spreadsheet
$kendo-spreadsheet-cell-editor-text: $kendo-spreadsheet-text !default;
/// The border color of the Spreadsheet cell editor.
/// @group spreadsheet
$kendo-spreadsheet-cell-editor-border: null !default;

/// The background color of the Spreadsheet resize handle.
/// @group spreadsheet
$kendo-spreadsheet-resize-handle-bg: k-color(primary) !default;

/// The border color of the Spreadsheet cell comment indicator.
/// @group spreadsheet
$kendo-spreadsheet-cell-comment-border: k-color(primary) !default;

/// The border color of the Spreadsheet cell dirty indicator.
/// @group spreadsheet
$kendo-spreadsheet-cell-dirty-border: k-color(error) !default;

/// The horizontal padding of the Spreadsheet sheets bar.
/// @group spreadsheet
$kendo-spreadsheet-sheets-bar-padding-x: k-spacing(2) !default;
/// The vertical padding of the Spreadsheet sheets bar.
/// @group spreadsheet
$kendo-spreadsheet-sheets-bar-padding-y: k-spacing(2) !default;
/// The border width of the Spreadsheet sheets bar.
/// @group spreadsheet
$kendo-spreadsheet-sheets-bar-border-width: 1px !default;

/// The border style of the Spreadsheet image Dialog.
/// @group spreadsheet
$kendo-spreadsheet-insert-image-dialog-border-style: dashed !default;
/// The border width of the Spreadsheet image Dialog.
/// @group spreadsheet
$kendo-spreadsheet-insert-image-dialog-border-width: 2px !default;
/// The bottom margin of the Spreadsheet image Dialog.
/// @group spreadsheet
$kendo-spreadsheet-insert-image-dialog-text-margin-bottom: k-spacing(7.5) !default;
/// The width of the Spreadsheet image Dialog preview.
/// @group spreadsheet
$kendo-spreadsheet-insert-image-dialog-preview-width: 355px !default;
/// The height of the Spreadsheet image Dialog preview.
/// @group spreadsheet
$kendo-spreadsheet-insert-image-dialog-preview-height: 230px !default;
/// The image of the Spreadsheet image Dialog preview.
/// @group spreadsheet
$kendo-spreadsheet-insert-image-dialog-preview-img: "data:image/png;base64," !default;
/// The border of the Spreadsheet image Dialog preview.
/// @group spreadsheet
$kendo-spreadsheet-insert-image-dialog-preview-border: k-color(border) !default;
/// The border radius of the Spreadsheet image Dialog preview.
/// @group spreadsheet
$kendo-spreadsheet-insert-image-dialog-preview-border-radius: k-border-radius(md) !default;
/// The shadow of the Spreadsheet image Dialog preview.
/// @group spreadsheet
$kendo-spreadsheet-insert-image-dialog-preview-overlay-shadow: inset 0 0 0 2000px color-mix(in srgb, k-color(inverse) 50%, transparent) !default;
/// The border radius of the Spreadsheet image Dialog preview overlay.
/// @group spreadsheet
$kendo-spreadsheet-insert-image-dialog-preview-overlay-border-radius: k-border-radius(md) !default;
/// The text color of the hovered Spreadsheet image Dialog preview.
/// @group spreadsheet
$kendo-spreadsheet-insert-image-dialog-overlay-hover-text: k-color(surface) !default;

/// The width of the Spreadsheet drawing handle.
/// @group spreadsheet
$kendo-spreadsheet-drawing-handle-width: 6px !default;
/// The height of the Spreadsheet drawing handle.
/// @group spreadsheet
$kendo-spreadsheet-drawing-handle-height: 6px !default;
/// The border style of the Spreadsheet drawing handle.
/// @group spreadsheet
$kendo-spreadsheet-drawing-handle-border-style: solid !default;
/// The border width of the Spreadsheet drawing handle.
/// @group spreadsheet
$kendo-spreadsheet-drawing-handle-border-width: 1px !default;
/// The border color of the Spreadsheet drawing handle.
/// @group spreadsheet
$kendo-spreadsheet-drawing-handle-outline-color: k-color(primary) !default;
/// The outline color of the Spreadsheet drawing handle.
/// @group spreadsheet
$kendo-spreadsheet-drawing-handle-border-color: k-color(primary) !default;
/// The border color of the Spreadsheet drawing handle.
/// @group spreadsheet
$kendo-spreadsheet-drawing-handle-bg: k-color(primary) !default;
/// The border radius of the Spreadsheet drawing handle.
/// @group spreadsheet
$kendo-spreadsheet-drawing-handle-border-radius: k-border-radius(lg) !default;

/// The outline style of the Spreadsheet drawing.
/// @group spreadsheet
$kendo-spreadsheet-drawing-outline-style: solid !default;
/// The outline width of the Spreadsheet drawing.
/// @group spreadsheet
$kendo-spreadsheet-drawing-outline-width: 2px !default;

/// The background color of the Spreadsheet drawing anchor.
/// @group spreadsheet
$kendo-spreadsheet-drawing-anchor-bg: color-mix(in srgb, k-color(secondary) 25%, transparent)  !default;

/// The vertical spacing of the Spreadsheet DropZone.
/// @group spreadsheet
$kendo-spreadsheet-dropzone-spacing-y: k-spacing(2) !default;

/// The padding of the Spreadsheet filter.
/// @group spreadsheet
$kendo-spreadsheet-filter-padding: k-spacing(1) !default;

/// The horizontal padding of the Spreadsheet filter menu link.
/// @group spreadsheet
$kendo-spreadsheet-filter-menu-link-padding-x: calc( #{$kendo-icon-size} + k-spacing(2) ) !default;

/// The margin of the Spreadsheet filter menu icon.
/// @group spreadsheet
$kendo-spreadsheet-filter-menu-icon-margin: 0 k-spacing(1) 0 calc(-1 * ( #{$kendo-icon-size} + #{k-spacing(1)} ) ) !default;

