@use "../core/_index.scss" as *;

// Badge

/// The width of the border around the Badge.
/// @group badge
$kendo-badge-border-width: 1px !default;

/// The horizontal padding of the small Badge.
/// @group badge
$kendo-badge-sm-padding-x: k-spacing(0.5) !default;
/// The horizontal padding of the medium Badge.
/// @group badge
$kendo-badge-md-padding-x: k-spacing(1) !default;
/// The horizontal padding of the large Badge.
/// @group badge
$kendo-badge-lg-padding-x: k-spacing(1.5) !default;

/// The vertical padding of the small Badge.
/// @group badge
$kendo-badge-sm-padding-y: k-spacing(0.5) !default;
/// The vertical padding of the medium Badge.
/// @group badge
$kendo-badge-md-padding-y: k-spacing(1) !default;
/// The vertical padding of the large Badge.
/// @group badge
$kendo-badge-lg-padding-y: k-spacing(1.5) !default;

/// The font sizes of the Badge.
/// @group badge
$kendo-badge-font-size: var( --kendo-font-size-xs, inherit ) !default;
/// The font size of the small Badge.
/// @group badge
$kendo-badge-sm-font-size: var( --kendo-font-size-xs, inherit ) !default;
/// The font size of the medium Badge.
/// @group badge
$kendo-badge-md-font-size: var( --kendo-font-size-xs, inherit ) !default;
/// The font size of the large Badge.
/// @group badge
$kendo-badge-lg-font-size: var( --kendo-font-size-xs, inherit ) !default;

/// The line heights used along with the $kendo-font-size variable.
/// @group badge
$kendo-badge-line-height: var( --kendo-line-height-xs, normal ) !default;
/// The line height used along with the $kendo-font-size variable of the small Badge.
/// @group badge
$kendo-badge-sm-line-height: var( --kendo-line-height-xs, normal ) !default;
/// The line height used along with the $kendo-font-size variable of the medium Badge.
/// @group badge
$kendo-badge-md-line-height: var( --kendo-line-height-xs, normal ) !default;
/// The line height used along with the $kendo-font-size variable of the large Badge.
/// @group badge
$kendo-badge-lg-line-height: var( --kendo-line-height-xs, normal ) !default;

/// The calculated minimum width of the circular Badge.
/// @group badge
$kendo-badge-min-width: calc( #{$kendo-badge-line-height } * 1em + calc( #{$kendo-badge-md-padding-y} * 2 ) + #{$kendo-badge-border-width * 2}) !default;
/// The calculated minimum width of the small circular Badge.
/// @group badge
$kendo-badge-sm-min-width: calc( #{$kendo-badge-sm-line-height } * 1em + calc( #{$kendo-badge-sm-padding-y} * 2 ) + #{$kendo-badge-border-width * 2} ) !default;
/// The calculated minimum width of the medium circular Badge.
/// @group badge
$kendo-badge-md-min-width: calc( #{$kendo-badge-md-line-height } * 1em + calc( #{$kendo-badge-md-padding-y} * 2 ) + #{$kendo-badge-border-width * 2} ) !default;
/// The calculated minimum width of the large circular Badge.
/// @group badge
$kendo-badge-lg-min-width: calc( #{$kendo-badge-lg-line-height  } * 1em + calc( #{$kendo-badge-lg-padding-y} * 2 ) + #{$kendo-badge-border-width * 2} ) !default;

/// The sizes map for the Badge.
/// @group badge
$kendo-badge-sizes: (
    sm: (
        padding-x: $kendo-badge-sm-padding-x,
        padding-y: $kendo-badge-sm-padding-y,
        font-size: $kendo-badge-sm-font-size,
        line-height: $kendo-badge-sm-line-height,
        min-width: $kendo-badge-sm-min-width
    ),
    md: (
        padding-x: $kendo-badge-md-padding-x,
        padding-y: $kendo-badge-md-padding-y,
        font-size: $kendo-badge-md-font-size,
        line-height: $kendo-badge-md-line-height,
        min-width: $kendo-badge-md-min-width
    ),
    lg: (
        padding-x: $kendo-badge-lg-padding-x,
        padding-y: $kendo-badge-lg-padding-y,
        font-size: $kendo-badge-lg-font-size,
        line-height: $kendo-badge-lg-line-height,
        min-width: $kendo-badge-lg-min-width
    )
) !default;

/// The theme colors of the Badge.
/// @group badge
$kendo-badge-theme-colors: (
    "primary": k-color(primary),
    "secondary": k-color(secondary),
    "tertiary": k-color(tertiary),
    "info": k-color(info),
    "success": k-color(success),
    "warning": k-color(warning),
    "error": k-color(error),
    "dark": k-color(dark),
    "light": k-color(light),
    "inverse": if($kendo-is-dark-theme, k-color(light), k-color(dark)),
) !default;

@forward "@progress/kendo-theme-core/scss/components/badge/_variables.scss" with (
    $kendo-badge-border-width: $kendo-badge-border-width,
    $kendo-badge-sm-padding-x: $kendo-badge-sm-padding-x,
    $kendo-badge-md-padding-x: $kendo-badge-md-padding-x,
    $kendo-badge-lg-padding-x: $kendo-badge-lg-padding-x,
    $kendo-badge-sm-padding-y: $kendo-badge-sm-padding-y,
    $kendo-badge-md-padding-y: $kendo-badge-md-padding-y,
    $kendo-badge-lg-padding-y: $kendo-badge-lg-padding-y,
    $kendo-badge-font-size: $kendo-badge-font-size,
    $kendo-badge-sm-font-size: $kendo-badge-sm-font-size,
    $kendo-badge-md-font-size: $kendo-badge-md-font-size,
    $kendo-badge-lg-font-size: $kendo-badge-lg-font-size,
    $kendo-badge-line-height: $kendo-badge-line-height,
    $kendo-badge-sm-line-height: $kendo-badge-sm-line-height,
    $kendo-badge-md-line-height: $kendo-badge-md-line-height,
    $kendo-badge-lg-line-height: $kendo-badge-lg-line-height,
    $kendo-badge-min-width: $kendo-badge-min-width,
    $kendo-badge-sm-min-width: $kendo-badge-sm-min-width,
    $kendo-badge-md-min-width: $kendo-badge-md-min-width,
    $kendo-badge-lg-min-width: $kendo-badge-lg-min-width,
    $kendo-badge-sizes: $kendo-badge-sizes,
    $kendo-badge-theme-colors: $kendo-badge-theme-colors
);
